.UserPageStyle {
  font-family: "Noto Sans", sans-serif;
  text-decoration: none;
  font-size: 13px;
  height: 100%;
}

.wrap {
  width: 100%;
  border: none;
}
.circle_box {
  position: relative;
}
.circle {
  stroke: #fff;
  fill: none;
}
.react-calendar__tile {
  height: 60px;
}
.react-calendar__tile svg {
  width: 70%;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  stroke: #fff;
  fill: none;
}
.CircularProgressbar .CircularProgressbar-path {
  stroke: #2047a2;
}
.react-calendar__tile--active {
  background-color: transparent;
  position: relative;
  color: #000;
}
.react-calendar__tile--active::after {
  position: absolute;
  content: "";
  font-size: 12px;
  left: 50%;
  bottom: 0px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  color: #000;
}
.react-calendar__navigation {
  width: 50%;
  margin: 0 auto;
  margin-bottom: 20px;
}
.react-calendar__navigation__label__labelText {
  font-size: 16px;
  font-weight: 500;
}
.react-calendar__month-view__weekdays__weekday {
  font-size: 15px;
  font-weight: 500;
  text-decoration: none;
}
abbr[title] {
  text-decoration: none;
}
.circle-background,
.circle-progress {
  fill: none;
}

.circle-background {
  stroke: #ddd;
}

.circle-progress {
  stroke: #3569d7;
}
.react-calendar__navigation__prev-button {
  color: #2047a2;
  font-size: 20px;
  font-weight: 500;
}
.react-calendar__navigation__next-button {
  color: #2047a2;
  font-size: 20px;
  font-weight: 500;
}
.react-calendar__navigation__next2-button {
  display: none;
}
.react-calendar__navigation__prev2-button {
  display: none;
}
.react-calendar__navigation button[disabled] {
  background-color: #fff;
  color: #1e1e1e;
  font-size: 15px;
  font-weight: 500;
}
button.react-calendar__tile--active:enabled:hover, button.react-calendar__tile--active:enabled:focus{
  background-color: #fff;
}
button.react-calendar__tile:enabled:hover, button.react-calendar__tile:enabled:focus{
  background-color: #fff;
}
* {
  font-family: "Noto Sans KR", sans-serif;
  text-decoration: none;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: black;
}
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
a:focus {
  text-decoration: none;
}
a:hover,
a:active {
  text-decoration: none;
}

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
::-webkit-scrollbar-thumb {
